<template>
  <div class="authors-table">
    <TypographyWithInfo
      font-weight="heavy"
      :info-popover-width="529"
      class="authors-table__title"
      info-text="Просим обратить внимание, что автор — это человек, творческим трудом которого была создана Технология. Оказание технической, финансовой, организационной или материальной поддержки не является творческим трудом и не делает лицо, оказавшее такую помощь, Автором.
В случае патентования (регистрации) Технологии персональные данные Авторов будут предоставлены патентным поверенным (на основании соглашения о неразглашении) и регистрационным органам."
    >
      Авторы Института
    </TypographyWithInfo>

    <VTable :items="authors" class="authors-table__table">
      <VTableCol title="№" name="number" :width="61" align="center" />
      <VTableCol title="ФИО" name="value" />

      <template v-slot:actions="props">
        <VTableCol
          :width="100"
          name="actions"
          title="Действия"
          class="authors-table__actions-cell"
        >
          <EditButton
            @click="onEdit(props.index)"
            class="authors-table__edit-button"
          />
          <DeleteButton
            @click="onDelete(props.index)"
            class="authors-table__delete-button"
          />
        </VTableCol>
      </template>
    </VTable>

    <AddButton @click="onClick"> Добавить автора </AddButton>

    <AuthorModal
      :initial-values="authors[editedAuthorIndex]"
      @close="onClose"
      :show="showModal"
      @submit="onSubmit"
    />
  </div>
</template>

<script>
import { mapMutations } from "vuex";

import VTable from "@/components/table/VTable";
import VTableCol from "@/components/table/VTableCol";
import AddButton from "@/components/buttons/AddButton";
import EditButton from "@/components/buttons/EditButton";
import AuthorModal from "@/components/authors/AuthorModal";
import DeleteButton from "@/components/buttons/DeleteButton";
import TypographyWithInfo from "@/components/ui/TypographyWithInfo";

export default {
  name: "AuthorsTable",
  components: {
    AddButton,
    DeleteButton,
    EditButton,
    VTable,
    VTableCol,
    AuthorModal,
    TypographyWithInfo,
  },
  data() {
    return {
      showModal: false,
      editedAuthorIndex: undefined,
    };
  },
  computed: {
    authors() {
      return this.$store.state.authorsForm.authors.map(
        ({ russianName, englishName }, idx) => ({
          number: idx + 1,
          value: `${russianName}/${englishName}`,
        })
      );
    },
  },
  methods: {
    ...mapMutations("authorsForm", [
      "addAuthor",
      "removeAuthor",
      "updateAuthor",
    ]),
    onClick() {
      this.showModal = true;
    },
    onClose() {
      this.showModal = false;
    },
    onSubmit(author) {
      if (this.editedAuthorIndex >= 0) {
        this.updateAuthor({ index: this.editedAuthorIndex, author });
        this.editedAuthorIndex = undefined;
      } else {
        this.addAuthor(author);
      }

      this.showModal = false;
    },
    onEdit(index) {
      this.editedAuthorIndex = index;
      this.showModal = true;
    },
    onDelete(index) {
      this.removeAuthor(index);
    },
  },
};
</script>

<style scoped lang="scss">
.authors-table {
  display: flex;
  flex-direction: column;
}

.authors-table__table {
  margin-bottom: 34px;
}

.authors-table__actions-cell {
  display: flex;
  align-items: center;
  justify-content: center;
}

.authors-table__edit-button {
  margin-right: 8px;
}

.authors-table__delete-button {
  margin-left: 8px;
}

.authors-table__title {
  margin-bottom: 35px;
}
</style>
