<template>
  <div class="other-authors-table">
    <TypographyWithInfo
      font-weight="heavy"
      :info-popover-width="529"
      class="other-authors-table__title"
      info-text="Укажите в таблице сведения об Авторах, не являющихся Авторами Института ниже. Если в создании Технологии иные Авторы участия не принимали, поставьте отметку «Не привлекались»"
    >
      Иные Авторы
    </TypographyWithInfo>

    <RadioGroup
      v-model="withOtherAuthors"
      class="other-authors-table__with-other-radio"
      :items="[
        { key: true, title: 'Привлекались' },
        { key: false, title: 'Не привлекались' },
      ]"
    />

    <AddButton @click="show = true" class="other-authors-table__add-button">
      Добавить автора
    </AddButton>

    <TypographyWithInfo
      font-weight="heavy"
      :info-popover-width="238"
      class="other-authors-table__title"
      info-text="В столбце сумма процентов по всем строкам должна составлять 100 (сто) %"
    >
      Сведения о творческом вкладе Авторов
    </TypographyWithInfo>

    <VTable :items="authors" class="authors-table__table">
      <VTableCol title="№" name="number" :width="61" align="center" />
      <VTableCol title="ФИО" name="value" />
      <VTableCol title="%" name="contribution" :width="60" />
      <VTableCol title="Должность" name="position" :width="0" />
      <VTableCol
        :width="0"
        name="workplace"
        title="Наименование Центра (место работы)"
      />
    </VTable>

    <OtherAuthorModal @close="show = false" :show="show" />
  </div>
</template>

<script>
import VTable from "@/components/table/VTable";
import VTableCol from "@/components/table/VTableCol";
import AddButton from "@/components/buttons/AddButton";
import TypographyWithInfo from "@/components/ui/TypographyWithInfo";
import OtherAuthorModal from "@/components/authors/OtherAuthorModal";
import RadioGroup from "@/components/ui/RadioGroup";
import { mapMutations } from "vuex";

export default {
  name: "OtherAuthorsTable",
  components: {
    VTable,
    VTableCol,
    AddButton,
    RadioGroup,
    OtherAuthorModal,
    TypographyWithInfo,
  },
  data() {
    return {
      show: false,
    };
  },
  computed: {
    authors() {
      return this.$store.state.authorsForm.otherAuthors;
    },
    withOtherAuthors: {
      get() {
        return this.$store.state.authorsForm.withOtherAuthors;
      },
      set(value) {
        this.changeWithOtherAuthors(value);
      },
    },
  },
  methods: {
    ...mapMutations("authorsForm", [
      "updateOtherAuthor",
      "removeOtherAuthor",
      "changeWithOtherAuthors",
    ]),
    onClick() {
      console.log("CLICK");
    },
  },
};
</script>

