<template>
  <FormsContainer class="general-form">
    <VTypography
      font-weight="heavy"
      font-style="header-1"
      class="general-form__header"
      >Общая информация</VTypography
    >

    <div class="general-form__row">
      <div class="general-form__col">
        <TextInput
          label="Направлено"
          id="DirectedInputId"
          :info-popover-width="266"
          placeholder="Русский текст"
          v-model="russianInformation"
          info-text="Фамилия Имя Отчество полностью"
        />
      </div>
      <div class="general-form__col">
        <TextInput
          v-model="englishInformation"
          placeholder="Английский текст"
        />
      </div>
    </div>
  </FormsContainer>
</template>

<script>
import { mapMutations } from "vuex";

import TextInput from "@/components/ui/TextInput";
import VTypography from "@/components/ui/Typography";
import FormsContainer from "@/components/forms/FormsContainer";

export default {
  name: "GeneralForm",
  components: { FormsContainer, VTypography, TextInput },
  methods: {
    ...mapMutations("generalForm", [
      "changeRussianInformation",
      "changeEnglishInformation",
    ]),
  },
  computed: {
    russianInformation: {
      get() {
        return this.$store.state.generalForm.russianInformation;
      },
      set(value) {
        this.changeRussianInformation(value);
      },
    },
    englishInformation: {
      get() {
        return this.$store.state.generalForm.englishInformation;
      },
      set(value) {
        this.changeEnglishInformation(value);
      },
    },
  },
};
</script>

<style scoped lang="scss">
.general-form__header {
  margin-bottom: 35px;
}

.general-form__row {
  display: flex;
}

.general-form__col {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;

  &:not(:last-child) {
    margin-right: 13px;
